.todo_item{
  display: flex;
  cursor: pointer;
  line-height: 40px;
}
.todo_item:hover{background-color:#ccc;}
.todo_item:hover .remove{
  opacity: 1;
}
.remove{
  opacity: 0;
  font-weight: bold;
  color:blue;
  margin: 0 0.5rem;
}
.tedo_text{
  flex: 1;
}

.checked{
  text-decoration: line-through;
  color:blue
}
.check_mark{
  font-size: 1.5rem;
  line-height: 1rem;
  color:red;
  font-weight: bold;
}
