.form{
  display: flex;
  width: 100%;
  margin: 0 auto;
  justify-content: space-around;
}
.form input{
  flex: 1;
  font-size: 1.25rem;
  border: none;
  outline: none;
  border-bottom: 1px solid rgb(0, 140, 255);
  
}
.create_btn{
  padding: 0.5rem 1.5rem;
  margin-left: 1rem;
  border: none;
  border-radius: 3px;
  color: #fff;
  font-weight: bold;
  font-size: 1rem;
  background: rgb(1, 61, 110);
}
.create_btn:hover{
  background: rgb(0, 140, 255);
}